import { Component, OnInit, ViewChild } from '@angular/core';
import { Validators, FormBuilder, FormGroup, ValidatorFn,AbstractControl} from '@angular/forms';
import { MasterService } from 'app/shared/services/master/master.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
// import { ChargerService } from '@shared/services/client/client.service';
import { ChargerService } from 'app/shared/services/charger/charger.service';
import { ChargerBatchService } from 'app/shared/services/charger-batch/charger-batch.service';
import { ChargingStationService } from 'app/shared/services/charging-station/charging-station.service';
import { VersionService } from 'app/shared/services/version/version.service';
import { ChargingModelService } from 'app/shared/services/charging-model/charging-model.service';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import {CountriesService} from 'app/shared/services/countries.service';
import { DatePipe } from '@angular/common';
import { DateService } from 'app/shared/services/utility/date.service';
import {MessageConstant} from 'app/shared/utils/message.constant';
import {ViewEncapsulation} from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}
@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations   : fuseAnimations,
  providers:[DatePipe]
})
export class CreateComponent implements OnInit {
  public date: moment.Moment;
  public disabled = false;
  public showSpinners = true;
  public showSeconds = true;
  public touchUi = false;
  public enableMeridian = false;
  minDate = new Date();
  public maxDate: moment.Moment;
  public stepHour = 1;
  public stepMinute = 1;
  public stepSecond = 1;

  form: FormGroup;
  batches:any;
  chargingModels:any;
  stations:any;
  versions :any;
  isAvailable = false;
  localStorage : any;
  connector_data :any;

  @ViewChild("toggleAvailable") toggleAvailable: any;
  filteredOptionsBatch: Observable<any[]>;
  filteredOptionsCharModel: Observable<any[]>;
  filteredOptionsCurrVersion: Observable<any[]>;
  filteredOptionCountry: Observable<any[]>;
  filteredOptionState: Observable<any[]>;
  filteredOptionCity: Observable<any[]>;
  countries:any;
  states:any;
  stateName:any;
  cities:any;
  pin_code_id:any;
  country_id:any;
  state_id:any;
  @ViewChild("toggleElement") ref: any;
  status = false;
 
  //messageConstant = MessageConstant;
  constructor(private fb: FormBuilder, 
    private chargerBatchService : ChargerBatchService, private chargingStationService : ChargingStationService, 
    private versionService : VersionService, private chargerService : ChargerService,
    private chargingModelService : ChargingModelService,
    private snackBar: MatSnackBar,public countryService: CountriesService,
    private router : Router, private datePipe: DatePipe,private dateService : DateService,) {
     
    
  }

  ngOnInit() {
    this.initialize();
  }

  initialize(){

    this.localStorage = JSON.parse( localStorage.getItem('userDetails'));

    this.form = this.fb.group({
      name: ['',[ Validators.required,Validators.pattern(/^[a-zA-Z0-9_#\- ]*$/)]  ],
      serialNo: ['', [Validators.required, Validators.pattern(/^[a-zA-Z0-9#]*$/)] ],
     // batch: ['', [autocompleteObjectValidator(),Validators.required]],
      chargingModel: ['', [autocompleteObjectValidator(),Validators.required]],
      station: [''],
      currentVersion: ['', [autocompleteObjectValidator()]],
      noOfGuns: [''],
      addressLine1:[''],
      addressLine2:[''],
      landmark:[''],
      city:[''],
      state:[''],
      country:[''],
      pincode:[''],   
      lat: ['', ],
      long: ['', ],
     
      otaConfig: ['', [ Validators.pattern(/^[a-zA-Z0-9]*$/) ]],
      periodicCheckRefTime: ['', ],
      periodicityInHours: ['', [ Validators.pattern(/^[0-9]*$/),Validators.max(48), Validators.min(0)]],
      whenToUpgrade: ['', [Validators.pattern(/^[a-zA-Z]*$/)]],
      upgradeSpecificTime: [''],
      isAvailable: [{value:'false'}, [Validators.required]],
      status : this.status == true ? 'Y' : 'N'
    });
   
   // this.getBatches();
    this.getChargingModels();
    this.getStations();
    this.getVersions();
    this.fillterOption();
    //this.getCountry();
    this.getAllCountry();
    this.getAllState();
    //this.getAllCity();
  }

  getAllCountry(){
    this.countryService.getAllCountry().subscribe(
      (data:any)=>{
        this.countries = data;
       
         this.filteredOptionCountry = this.form.controls.country.valueChanges
           .pipe(
             startWith(''),
             map(value => typeof value === 'string' ? value : value.name),
             map(name => name ? this._filterCountry(name) : this.countries)
           );
         },(err)=>{
           
         }
    )
  }
  getAllState(){
    this.countryService.getAllState().subscribe(
      (data:any)=>{
        this.states = data;
         this.filteredOptionState = this.form.controls.state.valueChanges
           .pipe(
             startWith(''),
             map(value => typeof value === 'string' ? value : value.name),
             map(name => name ? this._filterState(name) : this.states)
           );
         },(err)=>{
           
         }
    )
  }
  
  getAllCity(){
    this.countryService.getAllCities().subscribe(
      (data:any)=>{
        this.cities = data;
         this.filteredOptionCity = this.form.controls.city.valueChanges
           .pipe(
             startWith(''),
             map(value => typeof value === 'string' ? value : value.name),
             map(name => name ? this._filterCity(name) : this.cities)
           );
         },(err)=>{
           
         }
    )
  }
  getChargingModels(){
    this.chargingModelService.getChargingModels().subscribe(
      (data:any)=>{
        this.chargingModels = data;
        this.filteredOptionsCharModel = this.form.controls.chargingModel.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterCharModels(name) : this.chargingModels)
        );
      },(err)=>{
      }
    )
  }

  // getBatches(){
  //   this.chargerBatchService.getChargerBatches().subscribe(
  //     (data:any)=>{
        
  //       this.batches = data;
  //       this.filteredOptionsBatch = this.form.controls.batch.valueChanges
  //       .pipe(
  //         startWith(''),
  //         map(value => typeof value === 'string' ? value : value.name),
  //         map(name => name ? this._filterBatch(name) : this.batches)
  //       );
  //     },(err)=>{
  //     }
  //   )
  // }

  getStations(){
    this.chargingStationService.getChargingStations().subscribe(
      (data:any)=>{
        
        this.stations = data.data;
      },(err)=>{
        
      }
    )
  }

  getVersions(){
    this.versionService.getVersions().subscribe(
      (data:any)=>{
        
        this.versions = data;
        this.filteredOptionsCurrVersion = this.form.controls.currentVersion.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterCurrVersion(name) : this.versions)
        );
      },(err)=>{
        
      }
    )
  }

  onChangeChargingModel(selectedValue){
    const toSelect = this.chargingModels.find(c => c.id == selectedValue);
    this.connector_data = toSelect.connector_data;
    this.form.get('noOfGuns').setValue(toSelect.connector_data.length);
  }

  onSubmit(){
    
    if(this.form.valid){
      let formValues = this.form.value;
      if(formValues.otaConfig  == ''){
        formValues.otaConfig =  null;
      }
      if(formValues.periodicityInHours  == ''){
        formValues.periodicityInHours =  null;
      }
      if(formValues.whenToUpgrade  == ''){
        formValues.whenToUpgrade =  null;
      }
      if(formValues.upgradeSpecificTime  == ''){
        formValues.upgradeSpecificTime =  null;
      }

    
    let params = {
      name :formValues.name, 
      serial_no :formValues.serialNo, 
      //batch_id :formValues.batch.id, 
      model_id :formValues.chargingModel.id, 
      station_id : !!formValues.station ? formValues.station : 0, 
      current_version_id :formValues.currentVersion.id, 
      no_of_guns :formValues.noOfGuns, 
      //address1 : !!formValues.address ? formValues.address : '', 
      Lat :!!formValues.lat ? formValues.lat : 0, 
      Lng : !!formValues.long ? formValues.long : 0, 
      OTA_Config :formValues.otaConfig, 
      Periodic_Check_Ref_Time :this.datePipe.transform(formValues.periodicCheckRefTime, "yyyy-MM-dd HH:mm:ss") ,  //this.dateService.refineDate(formValues.periodicCheckRefTime),//this.datePipe.transform(formValues.periodicCheckRefTime, "yyyy-MM-dd'T'HH:mm:ss.SSS'Z'") , 
      Periodicity_in_hours :formValues.periodicityInHours, 
      When_to_Upgrade :formValues.whenToUpgrade, 
      Upgrade_Specific_Time :this.datePipe.transform(formValues.upgradeSpecificTime, "yyyy-MM-dd HH:mm:ss") ,//formValues.upgradeSpecificTime, 
      is_available :this.isAvailable == true ? 1 : 0, 
      status :  this.status == true ? 'Y' : 'N', 
      created_by : this.localStorage.userId,
      connector_data : this.connector_data,
      address1 :formValues.addressLine1,
      address2 :formValues.addressLine2,
      PIN: formValues.pincode,
      landmark: formValues.landmark,
      city_id : formValues.city.id,//formValues.city,
      state_id : formValues.state.id,
      country_id :formValues.country.id 
    }
    this.chargerService.create(params).subscribe(
      (data:any )=>{
        if(data.status){
          this.snackBar.open(MessageConstant.chargerNCreate, 'Dismiss', { duration: 5000 });
          this.router.navigate(['/assetManagement/chargerManagement/chargers/crud/list']);
        }else{
          this.snackBar.open(MessageConstant.dublicateCharger, 'Dismiss', { duration: 5000 });
        }
      },
      (err)=>{
        ////
        this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
      }
    )
    }
    else{
      return;
    }
    

  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
      ? 'Not a valid email'
      : '';
  }

  updateAvailable(event){
    this.isAvailable = event.checked;
    this.toggleAvailable.checked = this.isAvailable;
  }
  fillterOption(){
    
      this.filteredOptionsCharModel = this.form.controls.chargingModel.valueChanges
      .pipe(
        startWith(''),
        map(value => typeof value === 'string' ? value : value.name),
        map(name => name ? this._filterCharModels(name) : this.chargingModels)
      );
      this.filteredOptionsCurrVersion = this.form.controls.currentVersion.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterCurrVersion(name) : this.versions)
        );
  }
  displayBatch(item: any): string {
    return item && item.name ? item.name : '';
  }
  displayChargingModel(item: any): string {
    return item && item.name ? item.name : '';
  }
  displayCurrentVersion(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterBatch(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.batches.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  private _filterCharModels(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.chargingModels.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  private _filterCurrVersion(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.versions.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  getStateCountry(){
    
      this.pin_code_id = this.form.value.pincode;
      this.form.get('state').setValue( "");
      this.form.get('city').setValue( "");
      this.form.get('country').setValue("");
    this.countryService.getCountryStateByPIN(this.pin_code_id ).subscribe(
      (data:any)=>{
        //this.form.reset();
        if(data != undefined && data != null && data.length > 0){
          this.state_id = data[0].state_id;
          this.countryService.getCountryByState(this.state_id ).subscribe(
            (countrydata:any)=>{
              //this.form.reset();
              this.countries = countrydata;
              const toSelect = this.countries.find(c => c.id == data[0].country_id);
              this.form.get('country').setValue(toSelect);
              
              this.filteredOptionCountry = this.form.controls.country.valueChanges
              .pipe(
                startWith(''),
                map(value => typeof value === 'string' ? value : value.name),
                map(name => name ? this._filterCountry(name) : this.countries)
              );
              this.country_id = data[0].country_id;
              this.countryService.getStateByCountry(this.country_id ).subscribe(
                (statedata:any)=>{
                  //this.form.reset();
                  this.states = statedata;
                  const toSelect = this.states.find(c => c.id == data[0].state_id);
                  this.form.get('state').setValue(toSelect);
                  
                  this.filteredOptionState = this.form.controls.state.valueChanges
                  .pipe(
                    startWith(''),
                    map(value => typeof value === 'string' ? value : value.name),
                    map(name => name ? this._filterState(name) : this.states)
                  );
                  
                },(err)=>{
                  
                }
              )
              this.state_id = data[0].state_id;
              this.countryService.getCityByState(this.state_id ).subscribe(
                (Citydata:any)=>{
                  //this.form.reset();
                  this.cities = Citydata;
                  //const toSelect = this.states.find(c => c.id == data[0].state_id);
                 // this.form.get('state').setValue(toSelect);
                  
                  this.filteredOptionCity = this.form.controls.city.valueChanges
                  .pipe(
                    startWith(''),
                    map(value => typeof value === 'string' ? value : value.name),
                    map(name => name ? this._filterCity(name) : this.cities)
                  );
                  
                },(err)=>{
                  
                }
              )
            },(err)=>{
              
            }
          )
        }else{

        }
       
        
      },(err)=>{
        
      }
    )
  }
  onCountryChange(event){
    this.countries = [];
    this.getAllCountry();
    this.getStatesByCountry(event);
    
   
    //this.form.get('pincode').setValue( "");
    this.form.get('state').setValue( "");
    this.form.get('city').setValue( "");
  }
  getStatesByCountry(state){
     this.state_id = state.id;
     this.countryService.getStateByCountry(this.state_id ).subscribe(
      (statedata:any)=>{
        //this.form.reset();
        this.states = statedata;
       // const toSelect = this.states.find(c => c.id == data[0].state_id);
       // this.form.get('state').setValue(toSelect);
        
        this.filteredOptionState = this.form.controls.state.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterState(name) : this.states)
        );
        
      },(err)=>{
        
      }
    )
  }
  onStateChange(event){
    this.getCitiesByStates(event);
    this.getCountryByState(event);
    this.form.get('city').setValue( "");
  }
  getCitiesByStates(stateId){
    this.state_id = stateId.id;
     this.countryService.getCityByState(this.state_id ).subscribe(
      (citiesdata:any)=>{
        //this.form.reset();
        this.cities = citiesdata;
       // const toSelect = this.states.find(c => c.id == data[0].state_id);
       // this.form.get('state').setValue(toSelect);
        
        this.filteredOptionCity = this.form.controls.city.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterCity(name) : this.cities)
        );
        
      },(err)=>{
        
      }
    )
  }
  getCountryByState(stateId){
    this.state_id = stateId.id;
    this.countryService.getCountryByState(this.state_id ).subscribe(
      (countrydata:any)=>{
        //this.form.reset();
        this.countries = countrydata;
        const toSelect = this.countries.find(c => c.id == countrydata[0].id);
        this.form.get('country').setValue(toSelect);
        
        this.filteredOptionCountry = this.form.controls.country.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterCountry(name) : this.countries)
        );
       
     },(err)=>{
       
     }
   )
  }
  displayCountry(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterCountry(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.countries.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  displayState(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterState(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.states.filter(option => option.name.toLowerCase().includes(filterValue));
  }

  displayCity(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterCity(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.cities.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  updateStatus(event){
    this.status = event.checked;
    this.ref.checked = this.status;
  }
}


