import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ChargerCrudComponent } from './charger-crud/charger-crud.component';
import { TABS_DEMO_ROUTES } from './charger-crud/routes';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    {
        path : 'crud',component : ChargerCrudComponent,canActivate: [AuthGuard], children : TABS_DEMO_ROUTES
    },
    {
        path : 'listClient',component : ChargerCrudComponent,canActivate: [AuthGuard],
    },
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class ChargersRoutingModule{}